const env = require('./config.js'); //OSS keyIdKeySecret,timeout:87600;

const base64 = require('./base64.js');//Base64,hmac,sha1,crypto
require('./hmac.js');
require('./sha1.js');
const Crypto = require('./crypto.js');

/*
 *oss
 *@param - filePath :
 *@param - dir:
 *@param - successc:
 *@param - failc:
 */ 
const uploadFile = function (filePath, dir, successc, failc) {
  if (!filePath || filePath.length < 9) {
    uni.showModal({
      title: '',
      content: '',
      showCancel: false,
    })
    return;
  }
  
  //       + 150
  const aliyunFileKey = dir + new Date().getTime() + Math.floor(Math.random() * 150);
  
  const aliyunServerURL = env.uploadImageUrl;//OSShttps
  const accessid = env.OSSAccessKeyId;
  const policyBase64 = getPolicyBase64();
  const signature = getSignature(policyBase64);//
 
  uni.uploadFile({
    url: aliyunServerURL,// url
    filePath: filePath,//
    name: 'file',//file
    formData: {
      'key': aliyunFileKey,
      'policy': policyBase64,
      'OSSAccessKeyId': accessid,
      'signature': signature,
      'success_action_status': '200',
    },
    success: function (res) {
			console.log(res);
      if (res.statusCode != 200) {
        failc(new Error(':' + JSON.stringify(res)))
        return;
      }
       successc(aliyunServerURL+aliyunFileKey);
    },
    fail: function (err) {
      err.wxaddinfo = aliyunServerURL;
      failc(err);
    },
  })
}

const getPolicyBase64 = function () {
  let date = new Date();
  date.setHours(date.getHours() + env.timeout);
  let srcT = date.toISOString();
  const policyText = {
    "expiration": srcT, //Policypolicy 
    "conditions": [
      ["content-length-range", 0, 5 * 1024 * 1024] // ,5mb
    ]
  };

  const policyBase64 = base64.encode(JSON.stringify(policyText));
	console.log(policyBase64);
  return policyBase64;
}

const getSignature = function (policyBase64) {
  const accesskey = env.AccessKeySecret;

  const bytes = Crypto.HMAC(Crypto.SHA1, policyBase64, accesskey, {
    asBytes: true
  });
  const signature = Crypto.util.bytesToBase64(bytes);
console.log(signature);
  return signature;
}

module.exports = uploadFile;